/**
 * MotorDetailContainer.js
 * Detail Container for Motor
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { getFormValues } from 'redux-form';
import PropTypes from 'prop-types';

// Import Insurer Logos
import Logos from 'assets/images/InsurerLogos';

import MotorDetail from '../../components/Motor/MotorDetail';

import { getAnonymousSession, setInsuranceFormState, FORMSTATES } from '../../actions/insuranceActions';
import { getParams } from '../../actions/motorActions';
import { fbTag, gaTag, gaEvent, ampTag } from '../../lib/tagFunctions';

class MotorDetailContainer extends Component {
  componentDidMount() {
    const { setFormState } = this.props;
    setFormState(FORMSTATES.DETAILFORM.index);

    fbTag('PPS2 - Pageview - Motor - DetailForm', { Product: 'Motor' });
    gaTag({
      page_title: 'Motor - Detail Form',
      page_path: '/motor/detailform',
    });
    ampTag('MotorDetailsS');
  }

  render() {
    const {
      motorFields,
      plansData,
      selectedIndex,
      sessionId,
      checkOutOthers,
      isLoggedIn,
      piggyCallback,
      history,
    } = this.props;

    if (!plansData) {
      return null;
    }

    const selectedData = plansData[selectedIndex];

    let planDetails = {
      logo: Logos.others,
      logoDesc: '',
      planName: '',
      planTotal: null,
    };

    try {
      planDetails = {
        logo: Logos[selectedData.info.insurer_slug],
        logoDesc: `${selectedData.info.insurer_name}.svg`,
        planName: selectedData.info.product_name,
      };
    } catch (error) {
      console.log('Get plan details failed');
    }

    return (
      <MotorDetail
        motorFields={motorFields}
        selectedData={selectedData}
        planDetails={planDetails}
        sessionId={sessionId}
        checkOutOthers={checkOutOthers}
        isLoggedIn={isLoggedIn}
        piggyCallback={piggyCallback}
        changePlan={() => {
          history.goBack();
          fbTag('PPS2 - ButtonClick - Motor - DetailForm_ChangePlan', { Product: 'Motor', Click_Text: 'ChangePlan' });
          gaEvent('Motor - DetailForm - ChangePlan', {
            event_category: 'Motor',
            event_label: 'Motor - DetailForm - ChangePlan',
          });
        }}
      />
    );
  }
}

MotorDetailContainer.defaultProps = {
  sessionId: null,
  plansData: null,
};

MotorDetailContainer.propTypes = {
  plansData: PropTypes.array,
  selectedIndex: PropTypes.number.isRequired,
  motorFields: PropTypes.object.isRequired,
  setFormState: PropTypes.func.isRequired,
  sessionId: PropTypes.string,
  checkOutOthers: PropTypes.func.isRequired,
  isLoggedIn: PropTypes.bool.isRequired,
  piggyCallback: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
};


const mapStateToProps = state => ({
  motorForm: getFormValues('motor')(state),
  motorMoreForm: getFormValues('motor-more')(state),
  selectedPlanData: state.motor.selectedPlanData,
  plansData: state.motor.plansData,
  selectedIndex: state.motor.selectedIndex,
  sessionId: state.insurance.sessionId,
  motorFields: state.motor.motorFields,
});

const mapDispatchToProps = dispatch => ({
  getAnonymousSession: (params) => {
    dispatch(getAnonymousSession(params, true));
  },
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
});

const mergeProps = (propsFromState, propsFromDispatch, ownProps) => ({
  ...ownProps,
  ...propsFromState,
  ...propsFromDispatch,
  checkOutOthers: (detailForm) => {
    propsFromDispatch.getAnonymousSession(getParams(
      propsFromState.selectedPlanData,
      detailForm,
      propsFromState.motorForm,
      propsFromState.motorMoreForm,
    ));
  },
});


export default withRouter(connect(mapStateToProps, mapDispatchToProps, mergeProps)(MotorDetailContainer));
