/**
 * MotorDetail.js
 * Detail View for Motor
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import document from 'global/document';
import window from 'global/window';
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { change, reduxForm, getFormValues } from 'redux-form';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';

// Import Insurer Logos
import DetailForm from '../Templates/DetailForm/DetailForm';

import TextInput from '../Forms/TextInput/TextInput';
import DatePickerV2 from '../Forms/DatePicker/DatePickerV2';
import DropDownV2 from '../Forms/DropDown/DropDownV2';
import Selection from '../Forms/Selection/Selection';
import Spinner from '../Forms/Spinner/Spinner';
import CheckoutComplete from '../Templates/CheckoutPopup/CheckoutComplete';

import { setLoading, clearLoading } from '../../actions/loadingScreenActions';

import { slashedStringDateToDate } from '../../lib/dateFunctions';
import { validateForm, dateReValidate } from '../../lib/validators';
import { fbTag, gaEvent, ampTag } from '../../lib/tagFunctions';
// import { getPostal } from '../../actions/insuranceActions';

const formName = 'motor-detail';

class MotorDetail extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const {
      storeForm,
      motorFields,
      sessionId,
    } = nextProps;

    if (storeForm && storeForm.vehicle_make !== prevState.selectedVehicleMake) {
      const models = motorFields.car_models.options[storeForm.vehicle_make];
      if (models) {
        nextProps.change('vehicle_model', null);
        return ({
          selectedVehicleMake: storeForm.vehicle_make,
          selectedVehicleModels: models,
        });
      }
    }

    if (sessionId !== prevState.sessionId) {
      return ({
        sessionId,
      });
    }

    return null;
  }

  constructor(props) {
    super(props);

    const { storeForm, motorFields } = this.props;

    this.state = {
      sessionId: null,
      selectedVehicleMake: storeForm ? storeForm.vehicle_make: null,
      selectedVehicleModels: storeForm && storeForm.vehicle_make ? motorFields.car_models.options[storeForm.vehicle_make] : null,
      commencementDate: slashedStringDateToDate(motorFields.commencement_date.min_date),
      regYears: _.range(motorFields.reg_year.min, motorFields.reg_year.max + 1, 1),
      manufactureYears: _.range(motorFields.manufacture_year.max + 1, motorFields.manufacture_year.min, -1),
      claimsRange: _.range(0, motorFields.num_claims.max + 1, 1),
      passStartDate: slashedStringDateToDate(motorFields.pass_date.min_date),
      passEndDate: slashedStringDateToDate(motorFields.pass_date.max_date),
      errorStringData: {},
      errorArray: [],
    };

    this.handleSubmit = this.handleSubmit.bind(this);
    this.validateForm = this.validateForm.bind(this);
  }

  componentDidUpdate(prevProps) {
    const { sessionId } = this.state;
    const {
      storeForm,
      changeMotorForm,
      changeMotorMoreForm,
      loadingEnd,
    } = this.props;

    if (sessionId) {
      loadingEnd();
      setTimeout(() => {
        this.checkoutComplete.openCheckoutComplete();
        ampTag('MotorConfirmationS');
      }, 300);
    }

    if (storeForm !== prevProps.storeForm) {
      changeMotorForm('vehicle_make', storeForm.vehicle_make);
      changeMotorForm('vehicle_model', storeForm.vehicle_model);
      changeMotorForm('year_registered', storeForm.reg_year);

      changeMotorMoreForm('marital_status', storeForm.marital_status);
      changeMotorMoreForm('gender', storeForm.gender);
      changeMotorMoreForm('years_driving', storeForm.years_of_experience);
      changeMotorMoreForm('number_of_claims', storeForm.num_claims);
      changeMotorMoreForm('ncd', storeForm.ncd);
      changeMotorMoreForm('demerit_status', storeForm.offence_free);
    }
  }

  async validateForm(e, isSubmit = false) {
    const {
      selectedVehicleModels,
      regYears,
      manufactureYears,
      claimsRange,
      passStartDate,
      passEndDate,
      commencementDate,
    } = this.state;

    const { motorFields } = this.props;
    // console.log(e);
    const error = validateForm(e, [
      'email',
      'name',
      'nric',
      'gender',
      'postal',
      'marital_status',
      'birth_date',
      'pass_date',
      'years_of_experience',
      'ncd',
      'offence_free',
      'num_claims',
      'vehicle_number',
      'vehicle_usage',
      'vehicle_make',
      'vehicle_model',
      'num_seats',
      'reg_year',
      'manufacture_year',
      'off_peak',
      'commencement_date',
      'mobile_number',
    ], {
      pass_date: { type: 'DATE', start: passStartDate, end: passEndDate },
      years_of_experience: { type: 'ARRAY', data: motorFields.years_of_experience.options },
      ncd: { type: 'ARRAY', data: _.range(0, motorFields.ncd.max + 1, 10) },
      offence_free: { type: 'ARRAY', data: motorFields.offence_free.options },
      num_claims: { type: 'ARRAY', data: claimsRange },
      vehicle_usage: { type: 'ARRAY', data: motorFields.vehicle_usage.options },
      vehicle_make: { type: 'ARRAY', data: motorFields.car_brand.options },
      vehicle_model: { type: 'ARRAY', data: selectedVehicleModels },
      num_seats: { type: 'ARRAY', data: motorFields.num_seats.options },
      reg_year: { type: 'ARRAY', data: regYears },
      manufacture_year: { type: 'ARRAY', data: manufactureYears },
      off_peak: { type: 'ARRAY', data: motorFields.off_peak_car.options },
      commencement_date: { type: 'DATE', start: commencementDate },
    });

    /* if (isSubmit) {
      const postalResult = await getPostal(e.postal);
      if (!postalResult) {
        error.postal = 'Invalid postal code';
      }
    } */

    this.setState({
      errorStringData: error,
    });

    let errorArray = [];
    if (!_.isEmpty(error)) {
      errorArray = [<p className="error-string" key="error-1">Fields are not properly filled, please check again.</p>];
    }

    this.setState({ errorArray });

    return error;
  }

  async handleSubmit(e, isFirstValidate = true) {
    const { checkOutOthers, loading, storeForm } = this.props;
    const errorResult = await this.validateForm(isFirstValidate ? e : storeForm, true);
    const isEmptyResult = _.isEmpty(errorResult);

    if (dateReValidate(
      isEmptyResult,
      isFirstValidate,
      errorResult,
      error => this.setState({ errorStringData: error, errorArray: null }),
      () => this.handleSubmit(storeForm, false),
    )) {
      return;
    }

    if (isEmptyResult) {
      fbTag('PPS2 - ButtonClick - Motor - DetailForm_Checkout', { Product: 'Motor', Click_Text: 'Checkout' });
      gaEvent('Motor - DetailForm - Checkout', {
        event_category: 'Motor',
        event_label: 'Motor - DetailForm - Checkout',
      });
      ampTag('MotorCheckoutA', {
        'vehicle-type': e.vehicle_usage,
        manufacture: e.manufacture_year,
        'off-peak': e.off_peak,
      });

      loading();
      checkOutOthers(e);
    }
    window.scrollTo(0, document.body.scrollHeight);
  }

  render() {
    const {
      selectedVehicleModels,
      regYears,
      manufactureYears,
      claimsRange,
      passStartDate,
      passEndDate,
      errorStringData,
      commencementDate,
      errorArray,
    } = this.state;

    const {
      motorFields,
      form,
      planDetails,
      handleSubmit,
      isLoggedIn,
      piggyCallback,
      changePlan,
      storeForm,
    } = this.props;

    return (
      <Fragment>
        <DetailForm
          category="motor"
          isLoggedIn={isLoggedIn}
          changePlan={changePlan}
          handleSubmit={handleSubmit}
          onSubmit={this.handleSubmit}
          change={this.props.change}
          form={form}
          planDetails={planDetails}
          piggyCallback={piggyCallback}
          errorArray={errorArray}
        >
          <Row className="main-form" noGutters>
            <Col xs={12}>
              <TextInput
                form={form}
                change={this.props.change}
                name="email"
                placeholderString=""
                size="medium"
                zIndex={99}
                desc="Email Address"
                errorString={errorStringData.email}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            <Col className="right-padding" xs={12} sm={6}>
              <TextInput
                form={form}
                change={this.props.change}
                name="name"
                placeholderString=""
                size="medium"
                zIndex={98}
                desc="Full Name"
                errorString={errorStringData.name}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            <Col className="left-padding" xs={12} sm={6}>
              <TextInput
                form={form}
                change={this.props.change}
                name="nric"
                placeholderString=""
                size="medium"
                zIndex={97}
                desc="ID Number"
                errorString={errorStringData.nric}
                validateForm={() => this.validateForm(storeForm)}
                toUpper
              />
            </Col>
            <Col className="right-padding" xs={12} sm={6}>
              <Selection
                form={form}
                change={this.props.change}
                name="gender"
                type="text-selection"
                noEmptyTextSelection
                options={[
                  { key: 'Male', description: 'Male' },
                  { key: 'Female', description: 'Female' },
                ]}
                sizeInSM={6}
                desc="Gender"
                errorString={errorStringData.gender}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            <Col className="left-padding" xs={12} sm={6}>
              <TextInput
                form={form}
                change={this.props.change}
                name="postal"
                placeholderString=""
                size="medium"
                zIndex={96}
                desc="Postal Code"
                errorString={errorStringData.postal}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            <Col className="right-padding" xs={12} sm={6}>
              <Selection
                form={form}
                change={this.props.change}
                name="marital_status"
                type="text-selection"
                noEmptyTextSelection
                options={[
                  { key: 'Single', description: 'Single' },
                  { key: 'Married', description: 'Married' },
                ]}
                sizeInSM={6}
                desc="Marital Status"
                errorString={errorStringData.marital_status}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            <Col className="left-padding" xs={12} sm={6}>
              <DatePickerV2
                form={form}
                change={this.props.change}
                name="birth_date"
                size="medium"
                zIndex={95}
                desc="Date of birth"
                errorString={errorStringData.birth_date}
                validateForm={() => this.validateForm(storeForm)}
                endDate={new Date()}
              />
            </Col>
            <Col className="right-padding" xs={12} sm={6}>
              <TextInput
                form={form}
                change={change}
                name="mobile_number"
                errorString={errorStringData.mobile_number}
                validateForm={() => this.validateForm(storeForm)}
                placeholderString=""
                size="medium"
                zIndex={94}
                desc="Mobile Number"
              />
            </Col>
            <Col xs={12}>
              <hr className="hr-margin" />
            </Col>
            <Col xs={12} className="header">
              <p>Driving License</p>
            </Col>
            <Col className="right-padding" xs={12} sm={6}>
              <DatePickerV2
                form={form}
                change={this.props.change}
                name="pass_date"
                size="medium"
                zIndex={93}
                desc={motorFields.pass_date.display_name}
                startDate={passStartDate}
                endDate={passEndDate}
                errorString={errorStringData.pass_date}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            <Col className="left-padding" xs={12} sm={6}>
              <DropDownV2
                form={form}
                change={this.props.change}
                name="years_of_experience"
                placeholderString="Years ..."
                dropList={motorFields.years_of_experience.options}
                size="medium"
                zIndex={92}
                desc={motorFields.years_of_experience.display_name}
                errorString={errorStringData.years_of_experience}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            <Col className="right-padding" xs={12} sm={6}>
              <Spinner
                form={form}
                change={this.props.change}
                name="ncd"
                unit="%"
                min={0}
                max={motorFields.ncd.max}
                increment={10}
                initial={0}
                desc={motorFields.ncd.display_name}
                errorString={errorStringData.ncd}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            <Col className="left-padding" xs={12} sm={6}>
              <DropDownV2
                form={form}
                change={this.props.change}
                name="offence_free"
                placeholderString="Offence Free"
                dropList={motorFields.offence_free.options}
                size="medium"
                zIndex={91}
                desc={motorFields.offence_free.display_name}
                errorString={errorStringData.offence_free}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            <Col className="right-padding" xs={12} sm={6}>
              <DropDownV2
                form={form}
                change={this.props.change}
                name="num_claims"
                placeholderString="No of claims"
                dropList={claimsRange}
                size="medium"
                zIndex={90}
                desc={motorFields.num_claims.display_name}
                errorString={errorStringData.num_claims}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            <Col xs={12}>
              <hr className="hr-margin" />
            </Col>
            <Col xs={12} className="header">
              <p>Vehicle</p>
            </Col>
            <Col className="right-padding" xs={12} sm={6}>
              <TextInput
                form={form}
                change={this.props.change}
                name="vehicle_number"
                placeholderString=""
                size="medium"
                zIndex={89}
                desc="Vehicle number"
                errorString={errorStringData.vehicle_number}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            <Col className="left-padding" xs={12} sm={6}>
              <DropDownV2
                form={form}
                change={this.props.change}
                name="vehicle_usage"
                placeholderString="Vehicle usage"
                dropList={motorFields.vehicle_usage.options}
                size="medium"
                zIndex={88}
                desc={motorFields.vehicle_usage.display_name}
                errorString={errorStringData.vehicle_usage}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            <Col className="right-padding" xs={12} sm={6}>
              <DropDownV2
                form={form}
                change={this.props.change}
                name="vehicle_make"
                placeholderString="Brand"
                dropList={motorFields.car_brand.options}
                size="medium"
                zIndex={87}
                desc="Brand"
                errorString={errorStringData.vehicle_make}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            <Col className="left-padding" xs={12} sm={6}>
              <DropDownV2
                form={form}
                change={this.props.change}
                name="vehicle_model"
                placeholderString="Model"
                dropList={selectedVehicleModels}
                size="medium"
                zIndex={86}
                desc="Model"
                errorString={errorStringData.vehicle_model}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            <Col className="right-padding" xs={12} sm={6}>
              <DropDownV2
                form={form}
                change={this.props.change}
                name="num_seats"
                placeholderString="No of seats"
                dropList={motorFields.num_seats.options}
                size="medium"
                zIndex={85}
                desc={motorFields.num_seats.display_name}
                errorString={errorStringData.num_seats}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            <Col className="left-padding" xs={12} sm={6}>
              <DropDownV2
                form={form}
                change={this.props.change}
                name="reg_year"
                placeholderString="Year of registration"
                dropList={regYears}
                size="medium"
                zIndex={84}
                desc="Year of registration"
                errorString={errorStringData.reg_year}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            <Col className="right-padding" xs={12} sm={6}>
              <DropDownV2
                form={form}
                change={this.props.change}
                name="manufacture_year"
                placeholderString="Year of manufacture"
                dropList={manufactureYears}
                size="medium"
                zIndex={83}
                desc={motorFields.manufacture_year.display_name}
                errorString={errorStringData.manufacture_year}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            <Col className="left-padding" xs={12} sm={6}>
              <DropDownV2
                form={form}
                change={this.props.change}
                name="off_peak"
                placeholderString="Off-peak car"
                dropList={motorFields.off_peak_car.options}
                size="medium"
                zIndex={82}
                desc={motorFields.off_peak_car.display_name}
                errorString={errorStringData.off_peak}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            <Col className="right-padding" xs={12} sm={6}>
              <DatePickerV2
                form={form}
                change={this.props.change}
                name="commencement_date"
                size="medium"
                zIndex={81}
                startDate={commencementDate}
                desc={motorFields.commencement_date.display_name}
                errorString={errorStringData.commencement_date}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            <Col xs={12}>
              <hr className="hr-margin" />
            </Col>
          </Row>
        </DetailForm>
        <CheckoutComplete
          isQuoteOnly
          wrappedComponentRef={node => this.checkoutComplete = node}
        />
      </Fragment>
    );
  }
}

MotorDetail.defaultProps = {
  storeForm: null,
};

MotorDetail.propTypes = {
  storeForm: PropTypes.object,
  motorFields: PropTypes.object.isRequired,
  changeMotorForm: PropTypes.func.isRequired,
  changeMotorMoreForm: PropTypes.func.isRequired,
  checkOutOthers: PropTypes.func.isRequired,
  form: PropTypes.string.isRequired,
  change: PropTypes.func.isRequired,
  planDetails: PropTypes.object.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  isLoggedIn: PropTypes.bool.isRequired,
  piggyCallback: PropTypes.func.isRequired,
  changePlan: PropTypes.func.isRequired,
  loading: PropTypes.func.isRequired,
  loadingEnd: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  storeForm: getFormValues(formName)(state),
});

const mapDispatchToProps = dispatch => ({
  changeMotorForm: (key, value) => {
    dispatch(change('motor', key, value));
  },
  changeMotorMoreForm: (key, value) => {
    dispatch(change('motor-more', key, value));
  },
  loading: (callBack, errorMsg, time) => {
    dispatch(setLoading(callBack, errorMsg, time));
  },
  loadingEnd: () => {
    dispatch(clearLoading());
  },
});

export default reduxForm({
  form: formName,
  destroyOnUnmount: false,
})(connect(mapStateToProps, mapDispatchToProps)(MotorDetail));
